import React, { Component } from 'react'
import { CLOUDPATH_TAGGSHOP } from '../../../actions/api'
import './shopify-loading-screen.css'

export default class index extends Component {
  render() {
    return (
        <div className="shopiLoading_screen">
            <div className="d-flex flex-column-fluid align-items-center">
                <div className="container">
                    <div className="sl_screen text-center">
                        <h1 className="sl_screen_heading">
                            <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/img/shopify-loading-screen/wave-hand.png`} width="72" height="73 "alt="wave hand" />
                            Turn Your eCommerce Into a Visually Appealing Storefront in Minutes.
                        </h1>
                        <div class="row">
                            <div class="col-md-3">
                                <div className="shopify-video-card-box">
                                    <div className="shopify-video-card">
                                        <video className="lazy" poster={`${CLOUDPATH_TAGGSHOP}/app/assets/img/shopify-loading-screen/home_page-min.jpg`} playsinline="" frameborder="0" allowfullscreen="1" autoplay="1" loop="1" muted="1" data-origwidth="0" data-origheight="0"  width="440" height="960">
                                            <source src={`${CLOUDPATH_TAGGSHOP}/app/assets/img/shopify-loading-screen/home_page.mp4`} type="video/mp4"/>
                                        </video>
                                    </div>
                                    <div className="shopify-label-card">
                                        Homepage Gallery
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-3">
                                <div className="shopify-video-card-box">
                                    <div className="shopify-video-card">
                                    <video className="lazy" poster={`${CLOUDPATH_TAGGSHOP}/app/assets/img/shopify-loading-screen/product_page-min.jpg`} playsinline="" frameborder="0" allowfullscreen="1" autoplay="1" loop="1" muted="1" data-origwidth="0" data-origheight="0"  width="440" height="960">
                                            <source src={`${CLOUDPATH_TAGGSHOP}/app/assets/img/shopify-loading-screen/product_page.mp4`} type="video/mp4"/>
                                        </video>
                                    </div>
                                    <div className="shopify-label-card">
                                        Product Page gallery
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-3">
                                <div className="shopify-video-card-box">
                                    <div className="shopify-video-card">
                                    <video className="lazy" poster={`${CLOUDPATH_TAGGSHOP}/app/assets/img/shopify-loading-screen/category_page-min.jpg`} playsinline="" frameborder="0" allowfullscreen="1" autoplay="1" loop="1" muted="1" data-origwidth="0" data-origheight="0"  width="440" height="960">
                                            <source src={`${CLOUDPATH_TAGGSHOP}/app/assets/img/shopify-loading-screen/category_page.mp4`} type="video/mp4"/>
                                        </video>
                                    </div>
                                    <div className="shopify-label-card">
                                        Categery Based Gallery
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-3">
                                <div className="shopify-video-card-box">
                                    <div className="shopify-video-card">
                                    <video className="lazy" poster={`${CLOUDPATH_TAGGSHOP}/app/assets/img/shopify-loading-screen/shopon_bio-min.jpg`} playsinline="" frameborder="0" allowfullscreen="1" autoplay="1" loop="1" muted="1" data-origwidth="0" data-origheight="0"  width="440" height="960">
                                            <source src={`${CLOUDPATH_TAGGSHOP}/app/assets/img/shopify-loading-screen/shopon_bio.mp4`} type="video/mp4"/>
                                        </video>
                                    </div>
                                    <div className="shopify-label-card">
                                        Shopon.bio
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="spinner">
                            
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
  }
}